import React, { Component } from 'react';
import WardcardvitalsignChart from './Wardcardvitalsign'
import { statecolor } from '../Function/Wardstate'
import { hhmmssformattohalf, yyyymmddformat } from '../Function/Timeformat'
import './Wardchart.scss'



class WardChart extends Component {
    state = {
        data: this.simulation_data()
    }
    simulation_data() {
        var dataarray = [];
        const halftime = yyyymmddformat(new Date().getTime()) + ' ' + hhmmssformattohalf(new Date().getTime())
        const nowTime = new Date(halftime).getTime() - (24 * 60 * 60 * 1000)
        console.log(nowTime)
        for (var k = 0; k < 24; k++) {
            var dataset = []; //建立空的資料陣列
            for (var i = 0; i < 24; i++) {
                var newNum = Math.floor(Math.random() * 50) + 50;
                dataset.push(newNum);
            }
            const time = nowTime + (k * 60 * 60 * 1000)
            dataarray.push({
                'data': dataset,
                'time': time
            });
        }
        return dataarray;
    }
    render() {
        const data = this.state.data
        const { CMvalue } = this.props
        const vitalvalue = CMvalue
        return (
            <div className='Wardchartlayout'>
                <div className='halfwardchart'>
                    <div className='singlewardchart'>
                        <div className='firstRowTitle'>
                            <div>
                                <centerstyle>
                                    <t4 style={{ color: statecolor('HR') }}>HR(min)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "40px", color: statecolor('HR') }}>{vitalvalue.hr}</div>
                                </centerstyle>
                            </div>
                        </div>
                        <WardcardvitalsignChart svg_key={"svg_hr"} id_key={"div_hr"} data={data} axisBot={false} axisTop={true} colorshadow={"rgba(126, 211, 33, 0.1)"} circlrcolor={"#779F50"}></WardcardvitalsignChart>
                    </div>
                    <div className='singlewardchart'>
                        <div className='secondRowTitle'>
                            <div>
                                <centerstyle>
                                    <t4 style={{ color: statecolor('BP') }}>NBP(mmHg)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "18px", color: statecolor('BP') }}>{vitalvalue.nbpd}/{vitalvalue.nbps}</div>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "14px", color: statecolor('BP') }}>({vitalvalue.nbpm})</div>
                                </centerstyle>
                            </div>
                        </div>
                        <WardcardvitalsignChart svg_key={"svg_bp"} id_key={"div_bp"} data={data} data2={true} axisBot={false} axisTop={false} colorshadow={"rgba(240, 144, 144, 0.1)"} circlrcolor={"#C12528"} data_2_circlrcolor={"rgba(240, 144, 144, 0.5)"}></WardcardvitalsignChart>
                    </div>
                    <div className='singlewardchart'>
                        <div className='thirdRowTitle'>
                            <div>
                                <centerstyle>
                                    <t4 style={{ color: statecolor('BP') }}>ABP(mmHg)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "18px", color: statecolor('BP') }}>{vitalvalue.abpd}/{vitalvalue.abpd}</div>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "14px", color: statecolor('BP') }}>({vitalvalue.abpm})</div>
                                </centerstyle>
                            </div>
                        </div>
                        <WardcardvitalsignChart svg_key={"svg_abp"} id_key={"div_abp"} data={data} data2={true} axisBot={true} axisTop={false} colorshadow={"rgba(240, 144, 144, 0.1)"} circlrcolor={"#C12528"} data_2_circlrcolor={"rgba(240, 144, 144, 0.5)"}></WardcardvitalsignChart>
                    </div>
                </div>
                <div className='halfwardchart'>
                    <div className='singlewardchart'>
                        <div className='firstRowTitle'>
                            <div>
                                <centerstyle>
                                    <t4 style={{ color: statecolor('SpO2') }}>SpO2(%)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "40px", color: statecolor('SpO2') }}>{vitalvalue.spo2}</div>
                                </centerstyle>
                            </div>
                        </div>
                        <div>
                            <WardcardvitalsignChart svg_key={"svg_spo"} id_key={"div_spo"} data={data} axisBot={false} axisTop={true} colorshadow={"rgba(0, 183, 255, 0.1)"} circlrcolor={"#4773B8"}></WardcardvitalsignChart>
                        </div>
                    </div>
                    <div className='singlewardchart'>
                        <div className='secondRowTitle'>
                            <div>
                                <centerstyle>
                                    <t4 style={{ color: statecolor('BT') }}>BT(<sup>&ordm;</sup>C)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "40px", color: statecolor('BT') }}>{vitalvalue.bt}</div>
                                </centerstyle>
                            </div>
                        </div>
                        <WardcardvitalsignChart svg_key={"svg_bt"} id_key={"div_bt"} data={data} axisBot={false} axisTop={false} colorshadow={"rgba(199, 162, 244,0.1)"} circlrcolor={"#7F3DB5"}></WardcardvitalsignChart>
                    </div>
                    <div className='singlewardchart'>
                        <div className='thirdRowTitle'>
                            <div>
                                <centerstyle>
                                    <t4 style={{ color: statecolor('RR') }}>RR(min)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "40px", color: statecolor('RR') }}>{vitalvalue.rr}</div>
                                </centerstyle>
                            </div>
                        </div>
                        <WardcardvitalsignChart svg_key={"svg_rr"} id_key={"div_rr"} data={data} axisBot={true} axisTop={false} colorshadow={"rgba(255, 152, 0, 0.1)"} circlrcolor={"#DE7531"}></WardcardvitalsignChart>
                    </div>
                </div>
            </div>
        );
    }
}

export default WardChart;